@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

#page-content {
  color: #ccc;
  display: flex;
  justify-content: space-around;
  box-shadow: #ccc 0px 0px 19px 0px;
}

#page-content #cv-header {
  padding: 25px 15px;
  text-align: center;
  height: 580px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: 30%;
  justify-content: space-between;
  background-color: rgba(4, 122, 87, 0.4941176471);
}

#page-content #cv-header .my-photo {
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
}

#page-content #cv-header .my-photo img {
  display: block;
  width: 100%;
  object-fit: cover;
}

#page-content #cv-header .me {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

#page-content #cv-header .me h1 {
  padding-bottom: 8px;
}

#page-content #cv-header .me p {
  font-style: italic;
  font-size: 19px;
  padding-bottom: 30px;
}

#page-content #cv-header .me .social-block {
  display: flex;
  justify-content: space-evenly;
  width: 120px;
}

#page-content #cv-header .me .social-block a {
  text-decoration: none;
}

#page-content #cv-header .me .social-block a svg {
  width: 35px;
  height: 35px;
  fill: #242424;
}

#page-content #cv-header .me .social-block a:hover {
  filter: contrast(130%);
}

#page-content #cv-header .download {
  padding: 13px 30px;
  border-radius: 5px;
  border: 2px solid #ccc;
  color: #fff;
  background: transparent;
  cursor: pointer;
  font-size: 15px;
  margin-bottom: 20px;
  transition: 0.4s;
}

#page-content #cv-header .download:hover {
  color: #242424;
  background-color: #ccc;
}

#page-content #nav {
  flex-basis: 5%;
  background-color: #242424;
  border-right: 1px solid #ccc;
  border-left: 1px solid #ccc;
}

#page-content #nav .nav-container {
  height: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 25px 13px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#page-content #nav .nav-container .menu-btn {
  display: none;
  width: 50px;
  height: 50px;
  position: relative;
  cursor: pointer;
}

#page-content #nav .nav-container .menu-btn span {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 2px;
  margin-top: -1px;
  margin-left: -5px;
  background-color: #242424;
}

#page-content #nav .nav-container .menu-btn::before,
#page-content #nav .nav-container .menu-btn::after {
  background: #242424;
  content: "";
  display: block;
  position: absolute;
  margin-top: -1px;
  transition: 0.5s;
  width: 40px;
  height: 2px;
  transform: translateY(-10px);
  top: 50%;
  margin-left: 9px;
}

#page-content #nav .nav-container .menu-btn::after {
  transform: translateY(10px);
  width: 20px;
  margin-left: 29px;
}

#page-content #nav .nav-container .menu-btn.open span {
  background-color: transparent;
}

#page-content #nav .nav-container .menu-btn.open::before {
  transform: rotate(225deg);
  margin-left: 0px;
  width: 50px;
}

#page-content #nav .nav-container .menu-btn.open::after {
  transform: rotate(-45deg);
  width: 50px;
  margin-left: 0px;
}

#page-content #nav .nav-container #menu {
  z-index: 11;
}

#page-content #nav .nav-container #menu li {
  list-style-type: none;
}

#page-content #nav .nav-container #menu li a {
  padding-bottom: 20px;
  position: relative;
}

#page-content #nav .nav-container #menu li a svg {
  width: 35px;
  height: 35px;
  fill: #ccc;
  display: block;
}

#page-content #nav .nav-container #menu li a span {
  position: absolute;
  top: 2px;
  left: 0;
  width: 80px;
  color: transparent;
  transition: left 0.4s;
  padding: 7px;
}

#page-content #nav .nav-container #menu li:hover {
  filter: contrast(300%);
}

#page-content #nav .nav-container #menu li:hover span {
  color: #04A777;
  border: 1px solid #04A777;
  border-radius: 5px;
  filter: contrast(50%);
  display: block;
  left: 50px;
}

#page-content #nav .nav-container #menu li:nth-child(3) {
  stroke: #ccc;
}

#page-content #about-me {
  flex-basis: 65%;
  background-color: #242424;
}

#page-content #about-me .about-container {
  width: 100%;
  height: 580px;
  padding: 25px 15px;
  background-color: #242424;
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}

#page-content #about-me .about-container .welcome .hi {
  padding-bottom: 15px;
  font-size: 25px;
}

#page-content #about-me .about-container .welcome .introduce {
  font-size: 45px;
  padding-bottom: 15px;
}

#page-content #about-me .about-container .welcome .introduce span {
  color: #04A777;
}

#page-content #about-me .about-container .welcome .mind {
  font-style: italic;
  font-size: 20px;
}

#page-content #about-me .about-container .btn button {
  color: #ccc;
  width: 170px;
  height: 50px;
  border: none;
  background-color: transparent;
  position: relative;
  font-weight: bold;
  font-size: 17px;
  transition: 0.5s;
  cursor: pointer;
}

#page-content #about-me .about-container .btn button:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 4px solid #ccc;
  border-right: transparent;
  border-bottom: transparent;
  transition: 0.5s;
  border-radius: 4px;
}

#page-content #about-me .about-container .btn button:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 20px;
  height: 20px;
  border: 4px solid #04A777;
  border-left: transparent;
  border-top: transparent;
  transition: 0.5s;
  border-radius: 4px;
}

#page-content #about-me .about-container .btn button:hover {
  text-shadow: 1px 0 10px #ccc;
}

#page-content #about-me .about-container .btn button:hover::before {
  width: 105%;
  height: 110%;
}

#page-content #about-me .about-container .btn button:hover::after {
  width: 105%;
  height: 110%;
}

#portfolio {
  background-color: #242424;
  flex-basis: 65%;
  position: relative;
  overflow-y: scroll;
}

#portfolio::-webkit-scrollbar-track {
  background: #ccc;
}

#portfolio::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  overflow: hidden;
}

#portfolio::-webkit-scrollbar-thumb {
  background: #04A777;
}

#portfolio .portfolio-container {
  padding: 25px 20px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#portfolio .portfolio-container .works {
  display: flex;
  gap: 50px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 100px;
  padding-bottom: 40px;
  padding-top: 20px;
}

#portfolio .portfolio-container .works .work {
  flex-basis: 42%;
  width: 300px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.63) 0px 5px 15px;
}

#portfolio .portfolio-container .works .work .work-img {
  width: 100%;
  height: 230px;
}

#portfolio .portfolio-container .works .work .work-img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  opacity: 0.3;
  transition: opacity 0.6s;
}

#portfolio .portfolio-container .works .work .work-name {
  padding: 15px 0px;
  display: flex;
  align-items: center;
}

#portfolio .portfolio-container .works .work .work-name .style {
  width: 55px;
  height: 15px;
  background-color: #04A777;
}

#portfolio .portfolio-container .works .work .work-name h3 {
  font-size: 20px;
  padding-left: 20px;
  font-weight: normal;
}

#portfolio .portfolio-container .works .work:hover img {
  opacity: 0.7;
}

#contact {
  padding: 25px 15px;
  background-color: #242424;
  margin: 0 auto;
  flex-basis: 65%;
  display: flex;
  flex-direction: column;
}

#contact .my-contacts {
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
  height: 100%;
  padding-top: 90px;
}

#contact .my-contacts .my-contact {
  width: 250px;
  height: 120px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
}

#contact .my-contacts .my-contact svg {
  width: 40px;
  height: 40px;
  fill: #04A777;
}

#contact .my-contacts .my-contact h4 {
  font-size: 20px;
}

#contact .my-contacts .my-contact:nth-child(1),
#contact .my-contacts .my-contact:nth-child(2) {
  border-right: 1px solid #ccc;
}

#contact .my-contacts .my-contact div {
  max-width: 160px;
}

#contact .my-contacts .my-contact div h4 {
  word-wrap: break-word;
}

#resume {
  padding: 25px 10px;
  background-color: #242424;
  margin: 0 auto;
  flex-basis: 65%;
  display: flex;
  flex-direction: column;
}

#resume #autoTyping {
  padding-top: 40px;
  width: 100%;
}

#resume #autoTyping .colum {
  display: flex;
  width: 100%;
}

#resume #autoTyping .colum .number {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  color: #495162;
  font-size: 18px;
  padding: 0;
  width: 50px;
}

#resume #autoTyping .colum .content {
  color: #abb2b1;
  font-size: 20px;
  line-height: 24px;
}

#resume #autoTyping .colum .content .reserved {
  color: #3069a7;
}

#resume #autoTyping .colum .content .const-name {
  color: #A0C1DB;
}

#resume #autoTyping .colum .content .variables {
  color: #61a8c9;
}

#resume #autoTyping .colum .content .string {
  color: #b4ab54;
}

#me {
  background-color: #242424;
  flex-basis: 65%;
  overflow-y: scroll;
  position: relative;
  padding: 25px 20px;
}

#me::-webkit-scrollbar-track {
  background: #ccc;
}

#me::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  overflow: hidden;
}

#me::-webkit-scrollbar-thumb {
  background: #04A777;
}

#me .me-conteiner {
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: #242424;
  margin: 0 auto;
  margin-right: 20px;
  padding-bottom: 30px;
}

#me .me-conteiner .resume-header {
  display: flex;
  align-items: center;
  padding: 5px;
  justify-content: space-between;
  margin-top: 20px;
  border-bottom: 1px solid #ccc;
}

#me .me-conteiner .resume-header .resume-header--title {
  display: block;
  margin-right: 10px;
}

#me .me-conteiner .resume-header .resume-header--title h1 {
  padding-bottom: 3px;
}

#me .me-conteiner .resume-header .resume-header--title p {
  font-size: 18px;
  font-style: italic;
  color: #04A777;
}

#me .me-conteiner .resume-header .resume-header--contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#me .me-conteiner .resume-header .resume-header--contacts .download {
  background: #666;
  color: #fff;
  padding: 6px 12px 6px 20px;
  margin-bottom: 6px;
  text-decoration: none;
  font-size: 18px;
  width: 170px;
  text-align: center;
  position: relative;
  z-index: 1;
}

#me .me-conteiner .resume-header .resume-header--contacts .download::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgb(78, 78, 78);
  transition: width 0.3s;
  z-index: -1;
}

#me .me-conteiner .resume-header .resume-header--contacts .download::before {
  content: "";
  position: absolute;
  width: 0%;
  height: 100%;
  bottom: 0;
  right: 0;
  background: rgb(78, 78, 78);
  transition: width 0.3s;
  z-index: -1;
}

#me .me-conteiner .resume-header .resume-header--contacts .download:hover {
  color: #fff;
}

#me .me-conteiner .resume-header .resume-header--contacts .download:hover::after {
  width: 100%;
}

#me .me-conteiner .resume-header .resume-header--contacts .download:hover::before {
  width: 50%;
}

#me .me-conteiner .resume-header .resume-header--contacts .email-ref,
#me .me-conteiner .resume-header .resume-header--contacts .phone-ref {
  margin: 3px 0;
  transition: 0.3s;
}

#me .me-conteiner .resume-header .resume-header--contacts .social-block2 {
  width: 100%;
  justify-content: start;
  display: none;
}

#me .me-conteiner .resume-header .resume-header--contacts .social-block2 a {
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

#me .me-conteiner .resume-header .resume-header--contacts .social-block2 a svg {
  fill: #04A777;
}

#me .me-conteiner .resume-header .resume-header--contacts .social-block2 a:hover svg {
  fill: #047a57;
}

#me .me-conteiner .person {
  padding: 30px 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

#me .me-conteiner .person .my-photo {
  width: 220px;
  height: 220px;
  overflow: hidden;
  border-radius: 8%;
  margin-right: 20px;
}

#me .me-conteiner .person .my-photo img {
  display: block;
  width: 100%;
  object-fit: cover;
}

#me .me-conteiner .person .pers-info {
  min-width: 250px;
  padding: 20px;
}

#me .me-conteiner .person .pers-info p {
  font-size: 20px;
  padding-bottom: 10px;
}

#me .me-conteiner .person .pers-info p span {
  color: rgb(89, 172, 130);
  color: #04A777;
  padding-right: 5px;
  font-style: italic;
}

#me .me-conteiner .person .pers-info p:last-child {
  padding-bottom: 0px;
}

#me .me-conteiner .text-about-me {
  padding-top: 20px;
  padding-bottom: 30px;
}

#me .me-conteiner .text-about-me p {
  font-size: 19px;
  text-align: justify;
  letter-spacing: 0.5px;
}

#me .me-conteiner .title {
  font-size: 32px;
  padding: 10px 10px 20px 0px;
  font-style: italic;
}

#me .me-conteiner .education {
  margin-bottom: 30px;
}

#me .me-conteiner .education .educat-line {
  height: 90%;
  display: flex;
  align-items: space-between;
  justify-content: space-evenly;
}

#me .me-conteiner .education .educat-line .educate-time {
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#me .me-conteiner .education .educat-line .educate-time p {
  font-size: 20px;
}

#me .me-conteiner .education .educat-line .fill-line {
  background-color: rgba(4, 122, 87, 0.4941176471);
  width: 2px;
  height: 300px;
}

#me .me-conteiner .education .educat-line .fill-line .line-cover {
  width: 2px;
  max-height: 100%;
  height: 0%;
  background-color: #04A777;
}

#me .me-conteiner .education .educat-line .fill-line .line-cover ul {
  list-style-type: none;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

#me .me-conteiner .education .educat-line .fill-line .line-cover ul li {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #04A777;
  position: relative;
}

#me .me-conteiner .education .educat-line .fill-line .line-cover ul .active {
  background-color: #04A777;
}

#me .me-conteiner .education .educat-line .educate-name {
  width: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#me .me-conteiner .education .educat-line .educate-name p {
  font-size: 20px;
}

#me .me-conteiner .skills {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#me .me-conteiner .skills .languages {
  padding: 10px 10px 10px 0px;
}

#me .me-conteiner .skills .languages .lang-name {
  font-size: 30px;
  padding-bottom: 15px;
  font-style: italic;
}

#me .me-conteiner .skills .languages .language {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
}

#me .me-conteiner .skills .languages .language p {
  padding-right: 13px;
  font-size: 18px;
}

#me .me-conteiner .skills .languages .language progress {
  display: block;
  width: 250px;
  height: 9px;
  border-radius: 50px;
}

#me .me-conteiner .skills .languages .language progress::-webkit-progress-bar {
  background: #ccc;
  border-radius: 50px;
}

#me .me-conteiner .skills .languages .language progress::-webkit-progress-value {
  background: #04A777;
  border-radius: 50px;
  transition: all 0.9s;
}

#me .me-conteiner .skills .tools {
  height: 186px;
  padding: 10px;
}

#me .me-conteiner .skills .tools .toole-name {
  font-size: 30px;
  padding-bottom: 15px;
  font-style: italic;
}

#me .me-conteiner .skills .tools ul li {
  display: inline-block;
  background-color: #04A777;
  border-radius: 25px;
  color: #fff;
  padding: 5px 10px;
  margin: 8px 3px;
  font-size: 18px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

.page-name {
  text-align: center;
  font-size: 35px;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 10px;
}

p {
  line-height: 24px;
}

body {
  background-color: #424b55;
  padding: 35px;
}

body::-webkit-scrollbar {
  width: 7px;
  height: 7px;
  overflow: hidden;
}

body::-webkit-scrollbar-track {
  background: #ccc;
}

body::-webkit-scrollbar-thumb {
  background: #04A777;
}

a {
  color: #ccc;
  text-decoration: none;
  display: block;
}

a:hover {
  color: #04A777;
}

.btn-up {
  position: fixed;
  color: #04A777;
  right: 10px;
  bottom: 100px;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  background: #04A777;
  display: none;
  font-size: 30px;
  z-index: 33;
  border: 1px solid transparent;
  transition: all 0.2s;
}

.btn-up.showBtn {
  display: block;
}

@media screen and (max-width: 1310px) {
  #me .me-conteiner .skills {
    justify-content: center;
  }

  #me .me-conteiner .skills .languages {
    padding: 10px;
  }

  #me .me-conteiner .skills .languages .language progress {
    width: 210px;
  }

  #me .me-conteiner .skills .tools ul li {
    margin: 7px 1.5px;
  }
}

@media screen and (max-width: 1220px) {
  #contact .my-contacts .my-contact {
    width: 220px;
  }
}

@media screen and (max-width: 1170px) {
  #me .me-conteiner .resume-header .resume-header--title h1 {
    font-size: 30px;
  }
}

@media screen and (max-width: 1140px) {
  #resume #autoTyping {
    padding-top: 30px;
  }

  #resume #autoTyping .colum .number {
    font-size: 16px;
  }

  #resume #autoTyping .colum .content {
    font-size: 18px;
  }

  #me .me-conteiner .resume-header .resume-header--title h1 {
    font-size: 28px;
  }

  #me .me-conteiner .resume-header .resume-header--title p {
    font-size: 16px;
  }
}

@media screen and (max-width: 1124px) {
  #me .me-conteiner .skills {
    flex-direction: column;
    align-items: flex-start;
  }

  #me .me-conteiner .skills .languages {
    margin-bottom: 18px;
  }

  #me .me-conteiner .skills .languages .language {
    padding: 0px;
  }

  #me .me-conteiner .skills .languages .language progress {
    width: 250px;
  }

  #me .me-conteiner .skills .tools ul li {
    margin: 8px 3px;
  }
}

@media screen and (max-width: 1044px) {
  #contact .my-contacts .my-contact {
    width: 200px;
  }
}

@media screen and (max-width: 1024px) {
  #page-content #about-me .about-container .welcome .introduce {
    font-size: 40px;
  }

  #me .me-conteiner .resume-header .resume-header--title h1 {
    font-size: 25px;
  }

  .page-name {
    font-size: 30px;
  }
}

@media screen and (max-width: 990px) {
